.biography-section {
  padding: 4rem 0;
  background-color: #ffffff;
}

.biography-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 2rem;
}

.section-title {
  font-size: 2rem;
  font-weight: 600;
  color: #2d3748;
  margin-bottom: 2rem;
  position: relative;
  padding-bottom: 0.5rem;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: #4285f4;
}

.biography-content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.biography-paragraph {
  font-size: 1.1rem;
  line-height: 1.8;
  color: #4a5568;
  text-align: justify;
  font-weight: 400;
  letter-spacing: 0.01em;
}

.biography-paragraph:first-of-type {
  font-size: 1.15rem;
  color: #2d3748;
}

/* Responsive adjustments */
@media screen and (max-width: 768px) {
  .biography-section {
    padding: 3rem 0;
  }

  .biography-container {
    padding: 0 1.5rem;
  }

  .section-title {
    font-size: 1.75rem;
    margin-bottom: 1.5rem;
  }

  .biography-paragraph {
    font-size: 1rem;
    line-height: 1.7;
  }

  .biography-paragraph:first-of-type {
    font-size: 1.05rem;
  }
} 